.departmenthead {
  margin-top: 1vw;
}

.demaprtment_list {
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  width: 100%;
  min-height: 20rem;
  padding: 1rem;
}
.department_card_container {
  padding: 17.5px 23px;
  width: 100%;
  height: 70px;
  background-color: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 19px;
  margin-top: 19px;
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
}
.department_card_container:hover {
  background-color: rgba(75, 26, 133, 0.1);
}
.department_card_image {
  height: 48px;
  width: 48px;
}
.department_card_name {
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  color: #121212;
  margin-bottom: 0.3rem;
}

.department_card_head {
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-13);
  line-height: var(--line-height-17);
  color: rgba(18, 18, 18, 0.8);
  margin: 0;
}

.moderator_container_title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 1.5rem;
}

.with_search {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.with_search > h6 {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.9);
  margin-bottom: 0;
}

.search_container_input {
  position: relative;
  display: flex;
  align-items: center;
}

.search_container_input > input,
.search_container_input > input:focus {
  min-width: 300px;
  height: 40px;
  background: #ffffff;
  box-shadow: 1px 2px 32px rgba(0, 0, 0, 0.04);
  border-radius: 6px;
  border: none;
  padding-left: 3.075vw;
  outline: none;
}

.searchIcon {
  position: absolute;
  margin-left: 1.3vw;
  width: 1.5vw;
  top: 20%;
}

.class_container {
  width: 100%;
  min-height: 66px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  font-family: "Inter";
  font-style: normal;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
  margin-top: 1rem;
  padding: 0.8rem;
}
.class_container_text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.class_container_text > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: rgba(18, 18, 18, 0.8);
  margin-bottom: 0.2rem;
}
.class_container_text > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: rgba(18, 18, 18, 0.6);
  margin-bottom: 0.2rem;
}
.oneRowImage {
  width: 42px;
  height: 42px;
  border-radius: 50%;
}

.total_student_count {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #121212;
  margin-bottom: 0.2rem;
  margin-top: 1rem;
}
.total_other_count_container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 5rem;
}
.total_other_count_container > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: rgba(0, 0, 0, 0.8);
  margin-bottom: 0.2rem !important;
}

.student_container {
  width: 100%;
  min-height: 58px;
  background: #ffffff;
  border: 0.4px solid rgba(0, 0, 0, 0.15);
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 12px;
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  align-items: center;
  margin-top: 0.7rem;
  font-family: "Inter";
  font-style: normal;
  padding: 0.7rem;
}
.student_container > img {
  width: 42px;
  height: 42px;
  border-radius: 5px;
}

.student_info_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.6rem;
  width: 100%;
}
.student_info_text {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.student_info_text > h6 {
  font-weight: 500;
  font-size: 13px;
  line-height: 15px;
  color: #121212;
  margin: 0;
}
.student_info_text > p {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: 0 !important;
}

.select_application_btn {
  width: 50%;
  height: 47px;
  background: rgba(0, 26, 114, 0.15);
  border: 1px solid #001a72;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #001a72;
  margin-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  outline: transparent;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.promote_container {
  padding: 1rem;
  min-width: 40rem;
  max-width: 50rem;
  min-height: 20rem;
  max-height: 35rem;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  font-family: "Inter";
  font-style: normal;
}

.promote_student_card {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.8vw 2vw;
  border-bottom: 0.3px solid rgb(222, 214, 214);
  cursor: pointer;
  gap: 1.3rem;
}
.promote_student_card:hover,
.promote_student_cardActive {
  background: rgba(75, 26, 133, 0.1);
}

.promote_student_card_text {
  display: flex;
  gap: 1.5vw;
  align-items: center;
}

.promote_student_card_text > img {
  height: 2.5vw;
  width: 2.5vw;
  border-radius: 50%;
}
.promote_student_name {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.6rem;
}
.promote_student_name > p {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 1vw;
  line-height: 17px;
  margin-bottom: 0 !important;
  color: #121212;
}

.select_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.select_all_btn,
.select_all_btn:focus {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #001a72;
  outline: transparent;
  border: none;
  background-color: #ffffff;
}
.promote_all_btn {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  background: rgba(0, 26, 114, 0.15);
  border: 1px solid #001a72;
  box-shadow: -1px -1px 10px rgba(0, 0, 0, 0.02),
    1px 2px 32px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
  color: #001a72;
  outline: transparent;
  height: 35px;
}
.promote_container > h6 {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.9);
  margin-bottom: 0.5rem;
}
.promote_container > form > h6 {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: rgba(18, 18, 18, 0.8);
  text-align: center;
  cursor: pointer;
  margin-top: 1rem;
}
.promote_container_title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.promote_container_title > h6 {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.9);
  margin-bottom: 0.5rem;
}
.add_new_container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.add_new_exam {
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
  margin: 0;
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-14);
  line-height: var(--line-height-17);
  color: #436dfa;
  display: grid;
  place-items: end;
  cursor: pointer;
  margin-bottom: 0.5rem;
}
.menu_icon {
  width: 1.5rem;
  cursor: pointer;
  position: absolute;
  right: 3%;
}

/* // for id card  */
.items {
  margin-top: 0.5vw;
  border-radius: 6px;
  flex: 1;
}
.itemm {
  padding: 1vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  position: relative;
  cursor: pointer;
}

.checkbox {
  height: 20px;
  cursor: pointer;
  border-radius: 0;
}
.counts {
  display: flex;
  justify-content: space-between;
  padding: 1vw;
}

.counts > p {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 1vw;
  line-height: 17px;
  margin-bottom: 0 !important;
  color: #121212;
}
.promote_student_check_container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.promote_student_check {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1rem;
}
.department_checkbox {
  height: 1.3rem;
}
.promote_undo_container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.all_leave_config {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.settingbtnflex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.settingbtn {
  height: 1.5rem;
  cursor: pointer;
}
.rec_icon {
  width: auto;
  height: 1.5rem;
  cursor: pointer;
}
.tf_heading {
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: rgba(18, 18, 18, 0.9);
  margin-top: 0.7rem;
  margin-bottom: -0.5rem;
}
