.idcardsetting {
  padding-top: 1.5rem;
}

.rowflex {
  display: flex;
  justify-content: space-between;
}

.buttoncontainer {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.buttonInner {
  width: 100%;
}
/* 
  --------------Button-------------------- */
.button_main {
  width: 100%;
  min-height: 3.5vw;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.5vw;
  background: #1c023b;
  box-shadow: 1px 2px 32px rgba(0, 0, 0, 0.06);
  border-radius: 6px;
  margin: 0;
  outline: none;
  border: none;
}
.button_main:focus {
  outline: none;
}
.button_main > span {
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
  color: #ffffff;
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-16);
}

.idsamples {
  display: flex;

  width: 100%;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 3rem;
}

.idsamplecard {
  width: 15rem;
  height: 20rem;
  border: 0.01rem solid rgb(182, 180, 180);
  box-shadow: -6px 10px 15px -3px rgba(0, 0, 0, 0.1);
  border-radius: 0.4rem;
}

.idsamplecardActive,
.idsamplecard:hover {
  border: 0.01rem solid rgb(117, 142, 232);
  box-shadow: -8px 31px 59px -3px rgba(106, 142, 204, 0.1);
  transform: scale(1.1);
  cursor: pointer;
}

.idsamplecard > img {
  height: 100%;
  width: 100%;
  border-radius: 0.4rem;
}

.idsampleInputs {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-block: 3rem;
}

.colorpicker {
  width: 35rem;
  height: 30rem;
  background-color: #fff;
}
.colorpickerheader {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.colorpickerheader h6 {
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
  color: #384962;
  font-weight: var(--font-weight-500);
  font-size: var(--font-size-16);
}

.colorpickerheader p {
  font-family: var(--font-family);
  font-style: var(--font-style-normal);
  color: #112a50;
  font-weight: var(--font-weight-400);
  font-size: var(--font-size-12);
}
.colorpickerheader img {
  cursor: pointer;
}
.colorpickerbody {
  width: 100%;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.selectcorbtn {
  background-color: #4b687b;
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial,
    sans-serif;
  font-size: 14px;
  font-weight: 500;
  height: 40px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  outline: none;
  padding: 10px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: color 100ms;
  vertical-align: baseline;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  position: absolute;
  top: 18rem;
  right: 1rem;
}

.button-1:hover,
.button-1:focus {
  background-color: #82b0f0;
}
